ul {
  list-style: none;
}

.image-list {
  flex-wrap: wrap;
  gap: 12px;
  display: flex;
}

.image-item {
  width: calc(25% - 9px);
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.image:hover {
  transform: scale(1.03);
}

.image {
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  width: 100%;
  height: 100%;
}

.image-info {
  letter-spacing: .02em;
  text-align: center;
  font-size: 16px;
  line-height: 1.5;
}

